import React from 'react';
import SmallBadge from './SmallBadge';
import { Link } from 'react-router-dom';

//Custom hook
function useSearchBadges(badges) {
	//Uses hooks to handle badges filtering
	const [ query, setQuery ] = React.useState('')
	const [ filteredBadges, setFilteredBadges ] = React.useState(badges)

	//Memo hook to save previous searchs
	React.useMemo(() => {
		const result = badges.filter(badge =>{
			//Filters by first and last name
			return `${badge.firstName} ${badge.lastName}`.toLowerCase().
			includes(query.toLowerCase());
		});

		setFilteredBadges(result)
	}, [ badges, query ])

	return { query, setQuery, filteredBadges }
}


function BadgesList(props){
	//Gets badges from parent
	const { badges } = props

	//Gets data from custom hook
	const { query, setQuery, filteredBadges } = useSearchBadges(badges)

	//Search box
	const searchBox = <form>
					<div className="form-group">
						<label>Filter badges</label>
						<input 
							type="text" 
							className="form-control" 
							placeholder="Search a name..."
							value={query}
							onChange={(e)=>{
								setQuery(e.target.value)
							}}
							/>
					</div>
				</form>;

	//If no badges are found, returns this
	if (filteredBadges.length === 0) {
		return(
			<div>
			{searchBox}
				<h3>No badges were found</h3>
				<Link to="/badges/new" className="btn btn-primary">
					Create a badge
				</Link>
			</div>)
	}

	//Returns list of badges (Small version)
	return(
		<div className="BadgesList">
			{searchBox}
			<ul className="list-unstyled">
				{
					filteredBadges.map(badge =>{
						return(
							<li key={badge.id}>
								<Link className="text-reset text-decoration-none" 
									  to={`/badges/${badge.id}`}>
									<SmallBadge badge={badge}/>
								</Link>
							</li>
							)})
				}
		</ul>
	</div>
		)
	}


export default BadgesList;